@import '../../../globals';

.project-view {
  width: 100vw; 
  height: 100vh;
  display: flex;
  align-items: center;;

  &__content {
    background-image: linear-gradient(rgba(#000000, 0.4), rgba(#000000, 0.7)),
    url('../../static/work-space.jpg');
    background-size: cover;
    height: 60vh;
    width: 100vw;

    h3 {
      padding-top: 7rem; 
      color: $color-gold--main;
      font-size: 2rem;
      width: fit-content;
      margin: 0 auto;
      letter-spacing: 0.3rem;
    }

    h1 {
      padding-top: 1rem; 
      color: white; 
      font-size: 4rem;
      width: fit-content;
      margin: 0 auto;
      text-align: center;
    }
  }

  &__button { 
    width: fit-content;
    margin: 0 auto;
    padding-top: 7rem;
  }
}